    word_cloud_verb_title: '高频动词的词云分析',
    word_cloud_verb: '十九大报告是一个高瞻远瞩、务实创新的报告，报告系统总结了过去五年取得的伟大成就， 全面部署了当前和今后一段时期的任务要求。我们从动词的角度分析了我们党、国家、人民 在完成和即将开展的这些事业中的动作与姿态！在这个主题下，唯有用攥紧的拳头才能恰当表达 我们推进伟大事业的坚定决心和磅礴力量！“发展”“改革”“创新”“建设”“坚持”这些词表达出了我们 共同的心声：在中国共产党的全面领导下，坚持改革创新，拼搏发展，为实现中华民族伟大复兴的中国梦 而努力奋斗！'
